@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  @include m.tablet {
    min-height: 60rem;
  }

  &::before {
    content: '';
    background-color: v.$negro;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.6;
  }

  .contenido-header {
    position: relative;

    h1 {
      text-align: center;
      color: v.$blanco;
      text-transform: uppercase;
      margin-top: 5rem;
      @include m.tablet {
        margin-top: 10rem;
      }
    }
  }
}

//Header de cada página
.header-inicio {
  background-image: url(../img/header_inicio.webp);
}

.header-nosotros {
  background-image: url(../img/header_nosotros.webp);
}

.header-proceso {
  background-image: url(../img/header_proceso.webp);
}

.header-menu {
  background-image: url(../img/header_menu.webp);
}

.header-galeria {
  background-image: url(../img/header_galeria.webp);
}

.header-contacto {
